body {
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.dice-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 4%;
  margin: 20px 0;
  display: flex;
}

.dice {
  perspective: 1000px;
  width: 150px;
  height: 150px;
}

.cube {
  width: 150px;
  height: 150px;
  transform-style: preserve-3d;
  transition: transform 1s ease-in-out;
  position: relative;
  transform: rotateX(0)rotateY(0);
}

.cube div {
  background-position: center;
  background-size: cover;
  border: 1px solid #000;
  width: 150px;
  height: 150px;
  position: absolute;
}

.front {
  transform: rotateY(0)translateZ(75px);
}

.back {
  transform: rotateY(180deg)translateZ(75px);
}

.right {
  transform: rotateY(90deg)translateZ(75px);
}

.left {
  transform: rotateY(-90deg)translateZ(75px);
}

.top {
  transform: rotateX(90deg)translateZ(75px);
}

.bottom {
  transform: rotateX(-90deg)translateZ(75px);
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

input[type="number"] {
  text-align: center;
  width: 60px;
  padding: 5px;
  font-size: 16px;
}

@media (width <= 768px) {
  .dice, .cube, .cube div {
    width: 120px;
    height: 120px;
  }

  .front {
    transform: rotateY(0)translateZ(60px);
  }

  .back {
    transform: rotateY(180deg)translateZ(60px);
  }

  .right {
    transform: rotateY(90deg)translateZ(60px);
  }

  .left {
    transform: rotateY(-90deg)translateZ(60px);
  }

  .top {
    transform: rotateX(90deg)translateZ(60px);
  }

  .bottom {
    transform: rotateX(-90deg)translateZ(60px);
  }

  .dice-container {
    gap: 20px;
  }

  button {
    padding: 8px 16px;
    font-size: 14px;
  }

  input[type="number"] {
    width: 50px;
    font-size: 14px;
  }
}

@media (width <= 480px) {
  .dice, .cube, .cube div {
    width: 100px;
    height: 100px;
  }

  .front {
    transform: rotateY(0)translateZ(50px);
  }

  .back {
    transform: rotateY(180deg)translateZ(50px);
  }

  .right {
    transform: rotateY(90deg)translateZ(50px);
  }

  .left {
    transform: rotateY(-90deg)translateZ(50px);
  }

  .top {
    transform: rotateX(90deg)translateZ(50px);
  }

  .bottom {
    transform: rotateX(-90deg)translateZ(50px);
  }
}
